<i18n>
{
  "en": {
    "TakePortableBattery": "Take portable battery",
    "failedToEject": "Portable battery failed to eject, please scan the code again.",
    "RescanCode": "Rescan code"
  },
  "zh": {
    "TakePortableBattery": "取充电宝",
    "failedToEject": "充电宝弹出失败，请重新扫码",
    "RescanCode": "重新扫码"
  }
}
</i18n>

<template>
  <div class="takeFailure">
    <head-top :title='$t("TakePortableBattery")' :custom-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <img src="../../../assets/scanning_images/scan.png" alt="" v-if="Version === 1 || Version === 3">
      <img src="../../../assets/synerMax-skin/scan-small@3x.png" alt="" v-if="Version === 2">
      <span>{{ $t("failedToEject") }}</span>
      <button @click="goHandle" class="btn">{{ $t("RescanCode") }}</button>
    </div>
  </div>
</template>

<script>
import headTop from '../../../components/Header/index'
import { routerGo } from '../../../utils/router.js'
export default {
  name: 'takeFailure',
  data () {
    return {}
  },

  components: {
    headTop
  },

  methods: {
    goBackHander () {
      routerGo(-3)
    },
    goHandle () {
      routerGo(-4)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.takeFailure{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    img{
      height: 260px;
      margin: 80px auto 20px auto;
    }
    span{
      font-size: 30px;
      display: block;
      padding: 0 12%;
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      // box-shadow: 0px 5px 10px rgba(244, 218, 33, 0.6)
      box-shadow: 0px 4px 25px @themeColor;
    }
  }
}
</style>



